.defaultItem {
  margin: 1rem auto;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.7rem 1rem;
  font-size: 1.2rem;
  font-family: inherit;
  background-color: rgba(106, 190, 179, 0.575);
  border-radius: 10px;
  cursor: pointer;
}

.defaultItem:hover {
  opacity: 0.7;
}
